import { GeneralLayout } from "ui";
import { block } from "bem-cn";
import "./failure-page.scss";
import { Link, useParams } from "react-router-dom";

const b = block("pay-with-near-failure-page");

export function FailurePage() {
  const { id: paymentId } = useParams();

  return (
    <GeneralLayout className={b()}>
      <span className={b("message")}>Something went wrong</span>
      <Link className={b("link")} to={`/payment/${paymentId}`}>
        Please try again
      </Link>
    </GeneralLayout>
  );
}
